.navbar{
    display: flex;
    color: black;
    justify-content: space-between;
    background-color: white;
    align-items: center;
    font-size: 1.2rem;
    padding: 0 30px;
    box-shadow: 0 5px 15px rgba(0,0,0,0.25);
    width: 95%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 99;
}

.logo{
    cursor: pointer;
    justify-content: start;
    font-size: 2rem;
}
.logo>span{
    color: red;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    grid-gap: 50px;
    list-style: none;
    align-items: center;
    text-align: center;
    justify-content: end;
}

.nav-links{
    text-decoration: none;
    color: black;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    padding-right: 5px;
    cursor: pointer;
}

.nav-links:hover{
    background: rgb(80,80,238);
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}


.menu-icons{
    display: none;
}

.menu-icons{
    cursor: pointer;
}

@media screen and (max-width: 850px){
    .navbar{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: rgba(255,255,255,.25);
        box-shadow: 0 8px 32px 0 rgba(31,38,135,.37);
        backdrop-filter: blur(4px);
        border-radius: 13px;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -110%;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
        transition: all 0.3s ease-in-out;
    }

    .nav-menu.active{
        left: 0%;
    }
    

    .nav-links{
        display: block;
        widows: 100%;
        padding: 2rem 0;
        color: white;
    }

    .nav-links:hover{
        background: rgb(80,80,238);
        backdrop-filter: blur(20px);
        border-radius: 30px;
    }
    .menu-icons{
        display: block;
        cursor: pointer;
    }
}
